import uniIcons from '@/components/uni-icons/uni-icons.vue'
import uniSwipeAction from '@/components/uni-swipe-action/uni-swipe-action.vue'
import uniSwipeActionItem from '@/components/uni-swipe-action-item/uni-swipe-action-item.vue'

import moment from 'moment'

export default {
	components: {
		uniIcons,
		uniSwipeAction,
		uniSwipeActionItem
	},
	data() {
		return {
			posRight: '15px',
			messageList: [],
			userInfo:uni.getStorageSync("userInfo"),
			currentToken:null
		}
	},
	async onLoad() {
		const messageList = await this.$API.reqMessageList();
		messageList.forEach((item)=>{
			if(item.chatType==="single"){
				item.chatName = this.nameFilter(item.chatName)
				item.userFace = this.avatarUrlFilter(item.userFace)
			}
		})
		this.messageList = messageList;
		this.mapWebSocket();
	},
	filters: {
		timeFilter(value) {
			return moment(value).format("hh:mm")
		}
	},
	methods: {
		nameFilter(chatName) {
			return chatName.replace(this.userInfo.nickName, "").replace("<->", "")
		},
		avatarUrlFilter(avatarUrl) {
			return avatarUrl.replace(this.userInfo.avatarUrl, "").replace("<->", "")
		},
		toDetail(item){
			uni.navigateTo({
				url:"/pages/groupChat/groupChat?chatToken="+item.chatToken
			})
			this.currentToken = item.chatToken;
		},
		mapWebSocket(){
			uni.connectSocket({
				url:"ws://localhost:3000",
				header:{
					token:uni.getStorageSync("token")
				},
				protocols:["echo-protocol"]
			})
			uni.onSocketOpen(()=>{
				console.log('Open success')
			})
			uni.onSocketError(()=>{
				console.log('Error success')
			})
			uni.onSocketMessage(({data:info})=>{
				console.log('Message success',info)
				info = JSON.parse(info)
				if(info.chatToken===this.currentToken){
					this.$bus.$emit('sendMsg',info.data)
				}
			})
			uni.onSocketClose(()=>{
				console.log('Close success')
			})
		}
	}

}
